  width: 100%;
  height: 3px;
  background: #000000;
  transition: 0.3s;
}
.l-header-hamburger__bar:nth-child(1) {
  top: 0;
  transform: translateY(100%);
}
.l-header-hamburger__bar:nth-child(2) {
  top: 50%;
  transform: translateY(0%);
}
.l-header-hamburger__bar:nth-child(3) {
  top: 100%;
  transform: translateY(-100%);
}

.is-active .l-header-hamburger__bar {
  background: #ffffff;
}
.is-active .l-header-hamburger__bar:nth-child(1) {
  top: 50%;
  transform: translateY(-50%) rotate(405deg);
}
.is-active .l-header-hamburger__bar:nth-child(2) {
  top: 50%;
  transform: translateY(-50%) rotate(-405deg);
}
